import React from 'react';
import { getModels } from '../services/api';

export const DATE_TIME_VARIABLES = ['__date__', '__datetime__', '__time__'];
export const BUILT_IN_VARIABLES = ['__user__', ...DATE_TIME_VARIABLES];

export const BUILT_IN_OPTIONS = [
  {
    name: '__user__',
    title: 'Current user',
  },
  {
    name: '__date__',
    title: 'Current date',
  },
  {
    name: '__datetime__',
    title: 'Current datetime',
  },
  {
    name: '__time__',
    title: 'Current time',
  },
];

function getModelFilter(_domain, { search } = {}) {
  if (!_domain && !search) return null;
  return {
    ...(_domain ? { _domain } : {}),
    ...(search
      ? { criteria: [{ fieldName: 'name', operator: 'like', value: search }] }
      : {}),
  };
}

export function useMetaModelSearch(element, type = 'metaModel') {
  return React.useCallback(
    data => getModels(getModelFilter(element, data), type),
    [element, type]
  );
}
