import React, { useState, useEffect } from 'react';
import Paper from '@material-ui/core/Paper';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import IconButton from '@material-ui/core/IconButton';
import AddIcon from '@material-ui/icons/Add';
import DeleteIcon from '@material-ui/icons/Delete';
import { makeStyles } from '@material-ui/core/styles';

import produce from 'immer';
import moment from 'moment';
import flattenDeep from 'lodash/flattenDeep';
import isEmpty from 'lodash/isEmpty';

import ExpressionComponent from './expression-builder';
import { Timeline, Button, Select } from '../components';
import {
  COMBINATOR as COMBINATORS,
  MAP_OPERATOR,
  JOIN_OPERATOR,
  DATE_FORMAT,
  MAP_COMBINATOR,
  MAP_BPM_COMBINATOR,
  POSITIVE_OPERATORS,
  MANY_TO_ONE_TYPES,
  RELATIONAL_TYPES,
} from '../constants';
import {
  isBPMQuery,
  lowerCaseFirstLetter,
  getJsonExpression,
  jsStringEscape,
  getModelName,
} from '../utils';
import { DATE_TIME_VARIABLES } from './utils';
import { getRecord, getModels, saveRecord } from '../services/api';

const useStyles = makeStyles(theme => ({
  paper: {
    padding: theme.spacing(3, 2),
    width: `calc(100% - 40px)`,
    display: 'flex',
    '& > *': {
      height: '100%',
      width: '100%',
    },
  },
  expressionContainer: {
    display: 'flex',
    alignItems: 'center',
  },
  root: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'flex-start',
    height: 'calc(100% - 50px)',
    overflow: 'auto',
  },
  expression: {
    display: 'flex',
    alignItems: 'flex-start',
  },
  dialog: {
    minWidth: 300,
  },
  container: {
    height: '100%',
    overflow: 'hidden',
  },
  actions: {
    width: '100%',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
  },
  combinator: {
    width: 'fit-content',
  },
  save: {
    margin: theme.spacing(1),
    backgroundColor: '#0275d8',
    borderColor: '#0267bf',
    color: 'white',
    '&:hover': {
      backgroundColor: '#025aa5',
      borderColor: '#014682',
      color: 'white',
    },
  },
}));

let paramCount = 0;
let count = 0;

function ExpressionBuilder({
  parameters,
  onSave,
  exprVal,
  dialogActionButton,
}) {
  const {
    type: parentType = 'expressionBuilder',
    id,
    model,
    resultField,
    resultMetaField,
    resultParam,
    modelFilter,
    queryModel,
    withParam,
    isCondition,
    isPackage,
    isParameterShow = true,
  } = parameters || {};
  const expression = isBPMQuery(parentType) ? 'BPM' : 'GROOVY';
  const [combinator, setCombinator] = useState('and');
  const [record, setRecord] = useState(null);
  const [openAlert, setAlert] = useState(false);
  const [defaultExpressionValue, setDefaultExpressionValue] = useState(null);
  const [expressionComponents, setExpressionComponents] = useState([{}]);
  const classes = useStyles();

  function onAddExpressionEditor() {
    setExpressionComponents(
      produce(draft => {
        draft.push({
          value: isBPMQuery(parentType)
            ? queryModel && defaultExpressionValue
              ? defaultExpressionValue
              : {
                  metaModals:
                    expressionComponents &&
                    expressionComponents[0] &&
                    expressionComponents[0].value &&
                    expressionComponents[0].value.metaModals,
                  rules: [
                    {
                      id: 0,
                      parentId: -1,
                      combinator: 'and',
                      rules: [{}],
                    },
                  ],
                }
            : undefined,
        });
      })
    );
  }

  function onRemoveExpressionEditor(index) {
    setExpressionComponents(
      produce(draft => {
        draft.splice(index, 1);
      })
    );
  }

  function getRelationalCondition(
    rule,
    initValue = '',
    isParent = false,
    prefix
  ) {
    const map_operators =
      MAP_OPERATOR[isBPMQuery(parentType) ? 'BPM' : expression];
    const { fieldName: propFieldName, operator, allField } = rule;
    let {
      fieldValue,
      fieldValue2,
      isRelationalValue,
      relatedValueFieldName,
      relatedValueModal,
      relatedElseValueFieldName,
      relatedElseValueModal,
      parent,
      nameField,
    } = rule;
    let fieldName = propFieldName;
    const values =
      fieldName &&
      fieldName
        .split(JOIN_OPERATOR[isBPMQuery(parentType) ? 'BPM' : expression])
        .filter(f => f !== '');

    const fName = values[0];
    const field = allField.find(f => f.name === fName);
    const { targetName, selectionList, nameField: nameColumn } = field || {};
    const type =
      field && field.type && field.type.toLowerCase().replaceAll('-', '_');
    const typeName = field && field.typeName;
    const nestedFields = (values && values.splice(1)) || [];
    if (
      [
        'many_to_many',
        'one_to_many',
        'json_many_to_many',
        'json_one_to_many',
      ].includes(type)
    ) {
      const findRelational = initValue.match(/\$\$/g);
      if (findRelational && findRelational.length > 0) {
        const str =
          nestedFields.length >= 1
            ? `${fName}.find{it->it.$$$$}${
                POSITIVE_OPERATORS.includes(operator) ? ' != null' : ' == null'
              }`
            : `${fName}.${
                nestedFields.length > 0 ? 'find' : 'collect'
              }{it->it$$$$}${
                nestedFields.length > 0
                  ? POSITIVE_OPERATORS.includes(operator)
                    ? ' != null'
                    : ' == null'
                  : ''
              }`;
        initValue = initValue.replace(/\$\$/g, str);
      } else {
        const str =
          nestedFields.length >= 1
            ? `${fName}.find{it->it.$$}${
                POSITIVE_OPERATORS.includes(operator) ? ' != null' : ' == null'
              }`
            : `${fName}.${
                nestedFields.length > 0 ? 'find' : 'collect'
              }{it->it$$}${
                nestedFields.length > 0
                  ? POSITIVE_OPERATORS.includes(operator)
                    ? ' != null'
                    : ' == null'
                  : ''
              }`;
        initValue += str;
      }
      const nestedFieldName = nestedFields.join(JOIN_OPERATOR[expression]);
      return getRelationalCondition(
        {
          fieldName: nestedFieldName,
          operator,
          allField,
          fieldValue,
          fieldValue2,
          isRelationalValue,
          relatedValueFieldName,
          relatedValueModal,
          relatedElseValueFieldName,
          relatedElseValueModal,
          parent: values && values[0],
          nameField: nameColumn || nameField,
        },
        initValue,
        nestedFields.length > 1,
        prefix
      );
    } else if (
      [
        'json_many_to_one',
        'json_one_to_one',
        'many_to_one',
        'one_to_one',
      ].includes(type)
    ) {
      const nestedFieldName = nestedFields.join(JOIN_OPERATOR[expression]);
      const findRelational = initValue.match(/\$\$/g);
      const str =
        nestedFields.length >= 1
          ? `${fName}${JOIN_OPERATOR[expression]}`
          : fName;
      if (findRelational && findRelational.length > 0) {
        initValue = initValue.replace(/\$\$/g, `${str}$$$$`);
      } else {
        initValue += `${str}$$`;
      }
      return getRelationalCondition(
        {
          fieldName: nestedFieldName,
          operator,
          allField,
          fieldValue,
          fieldValue2,
          isRelationalValue,
          relatedValueFieldName,
          relatedValueModal,
          relatedElseValueFieldName,
          relatedElseValueModal,
          nameField: nameColumn || nameField,
          parent: values && values[0],
        },
        initValue,
        nestedFields.length > 1,
        prefix
      );
    } else {
      const isNumber = ['long', 'integer', 'decimal', 'boolean'].includes(type);
      const isDateTime = ['date', 'time', 'datetime'].includes(type);

      if (isNumber) {
        if (
          !fieldValue &&
          fieldValue !== false &&
          !selectionList &&
          !isRelationalValue
        ) {
          fieldValue = 0;
        }
        if (['between', 'notBetween'].includes(operator) && !fieldValue2) {
          fieldValue2 = 0;
        }
      }

      if (!isRelationalValue && !isNumber && typeof fieldValue !== 'object') {
        fieldValue = `'${jsStringEscape(fieldValue, withParam)}'`;
        fieldValue2 = `'${jsStringEscape(fieldValue2, withParam)}'`;
      }

      if (isDateTime) {
        if (!isRelationalValue) {
          fieldValue = getDateTimeValue(type, fieldValue);
          fieldValue2 = getDateTimeValue(type, fieldValue2);
        }
        fieldName = typeName ? `${fieldName}?.toLocalDateTime()` : fieldName;
      }
      if (['in', 'notIn'].includes(operator)) {
        const isManyToManyField = initValue && initValue.includes('{it->it$$}');
        const field = allField.find(f => f.name === parent) || {};
        const value =
          typeof (rule && rule.fieldValue) === 'string'
            ? rule.fieldValue
            : rule.fieldValue
                .map(i =>
                  isNumber
                    ? `${
                        i['nameField'] ||
                        i['targetName'] ||
                        i['fullName'] ||
                        i['name'] ||
                        i['id']
                      }`
                    : i['nameField'] ||
                      i['targetName'] ||
                      i['fullName'] ||
                      i['name']
                    ? `'${
                        i['nameField'] ||
                        i['targetName'] ||
                        i['fullName'] ||
                        i['name']
                      }'`
                    : i['id']
                )
                .join(',');
        const name =
          isParent || nestedFields.length >= 1
            ? ''
            : `${fieldName}${
                selectionList
                  ? ''
                  : `${JOIN_OPERATOR[expression]}${
                      nameField ||
                      (field && field.targetName) ||
                      targetName ||
                      'fullName'
                    }`
              }`;
        const str = `${operator === 'notIn' ? '!' : ''}${`(${value})`}${
          JOIN_OPERATOR[expression]
        }${map_operators[operator]}${isManyToManyField ? 'All' : ''}(${prefix}${
          JOIN_OPERATOR[expression]
        }${initValue.replace(/\$\$/g, name)})`;
        return str;
      } else if (['contains', 'notContains'].includes(operator)) {
        const isManyToManyField = initValue && initValue.includes('{it->it$$}');
        const field = allField.find(f => f.name === parent) || {};
        const value =
          typeof (rule && rule.fieldValue) === 'string'
            ? rule.fieldValue
            : rule.fieldValue &&
              rule.fieldValue
                .map(i =>
                  isNumber
                    ? `${
                        i['nameField'] ||
                        i['targetName'] ||
                        i['fullName'] ||
                        i['name'] ||
                        i['id']
                      }`
                    : i['nameField'] ||
                      i['targetName'] ||
                      i['fullName'] ||
                      i['name']
                    ? `'${
                        i['nameField'] ||
                        i['targetName'] ||
                        i['fullName'] ||
                        i['name']
                      }'`
                    : i['id']
                )
                .join(',');
        const name =
          isParent || nestedFields.length >= 1
            ? ''
            : `${fieldName}${
                selectionList
                  ? ''
                  : `${JOIN_OPERATOR[expression]}${
                      nameField ||
                      (field && field.targetName) ||
                      targetName ||
                      'fullName'
                    }`
              }`;
        const str = `${operator === 'notContains' ? '!' : ''}(${prefix}${
          JOIN_OPERATOR[expression]
        }${initValue.replace(/\$\$/g, name)})${JOIN_OPERATOR[expression]}${
          map_operators[operator]
        }${isManyToManyField ? 'All' : ''}(${value})`;
        return str;
      } else if (['between', 'notBetween'].includes(operator)) {
        const temp = initValue.match(/it.\$\$/g);
        if (temp && temp.length) {
          const str = `(it.${prefix}${JOIN_OPERATOR[expression]}${fieldName} >= ${fieldValue} && it.${prefix}${JOIN_OPERATOR[expression]}${fieldName} <= ${fieldValue2})`;
          if ('notBetween' === operator) {
            return `${initValue.replace(/it.\$\$/g, `!${str}`)}`;
          }
          return initValue.replace(/it.\$\$/g, str);
        } else {
          const replace = p1 => {
            const field = (p1 && p1.replace(/\$\$/g, fieldName)) || fieldName;
            if ('notBetween' === operator) {
              return `!(${prefix}${JOIN_OPERATOR[expression]}${field} >= ${fieldValue} && ${prefix}${JOIN_OPERATOR[expression]}${field} <= ${fieldValue2})`;
            }
            return `(${prefix}${JOIN_OPERATOR[expression]}${field} >= ${fieldValue} && ${prefix}${JOIN_OPERATOR[expression]}${field} <= ${fieldValue2})`;
          };
          return replace(initValue);
        }
      } else if (['isNotNull', 'isNull'].includes(operator)) {
        const str = `${fieldName} ${map_operators[operator]}`;
        const field = allField.find(f => f.name === parent) || {};
        const isManyToManyField = initValue && initValue.includes('{it->it$$}');
        if (isManyToManyField) {
          const name =
            isParent || nestedFields.length >= 1
              ? ''
              : `${fieldName}${
                  selectionList
                    ? ''
                    : `${JOIN_OPERATOR[expression]}${
                        (field && field.targetName) || targetName || 'fullName'
                      }`
                }`;
          return `${prefix}${JOIN_OPERATOR[expression]}${initValue.replace(
            /\$\$/g,
            `${name} ${str}`
          )}`;
        }
        return `${prefix}${JOIN_OPERATOR[expression]}${initValue.replace(
          /\$\$/g,
          str
        )}`;
      } else if (['isTrue', 'isFalse'].includes(operator)) {
        const value = operator === 'isTrue' ? true : false;
        const str = `${fieldName} ${map_operators[operator]} ${value}`;
        return `${prefix}${JOIN_OPERATOR[expression]}${initValue.replace(
          /\$\$/g,
          str
        )}`;
      } else if (['like', 'notLike'].includes(operator)) {
        const str = `${fieldName}${JOIN_OPERATOR[expression]}${map_operators[operator]}(${fieldValue})`;
        return `${operator === 'notLike' ? '!' : ''}${prefix}${
          JOIN_OPERATOR[expression]
        }${initValue.replace(/\$\$/g, str)}`;
      } else {
        let fieldNew = field || allField.find(f => f.name === parent) || {};
        let value =
          typeof fieldValue === 'object' && fieldValue
            ? `'${jsStringEscape(
                fieldValue[nameField] ||
                  fieldValue[targetName] ||
                  fieldValue['fullName'] ||
                  fieldValue['name'] ||
                  fieldValue['id'] ||
                  '',
                withParam
              )}'`
              ? `'${jsStringEscape(
                  fieldValue[nameField] ||
                    fieldValue[targetName] ||
                    fieldValue['fullName'] ||
                    fieldValue['name'] ||
                    fieldValue['id'] ||
                    '',
                  withParam
                )}'`
              : fieldValue['name']
            : fieldValue;
        const str = `${
          typeof fieldValue === 'object' && fieldValue
            ? `${fieldName}${JOIN_OPERATOR[expression]}${
                nameField || fieldNew.targetName || 'fullName'
              }`
            : fieldName
        } ${map_operators[operator]} ${value}`;
        return `${prefix}${JOIN_OPERATOR[expression]}${initValue.replace(
          /\$\$/g,
          str
        )}`;
      }
    }
  }

  function getDateTimeValue(type, fieldValue, isJsonField = false) {
    const isQuery = isBPMQuery(parentType);
    if (
      ['DAY', 'MONTH', 'YEAR', 'HOUR', 'MINUTE', 'SECOND'].includes(
        fieldValue?.name
      )
    ) {
      return fieldValue;
    }
    if (type === 'date') {
      let date = `'${moment(fieldValue, DATE_FORMAT['date']).format(
        'YYYY-MM-DD'
      )}'`;
      if (isJsonField || isQuery) {
        return date;
      }
      return `LocalDate.parse(${date})`;
    } else if (type === 'datetime') {
      if (isJsonField) {
        return `'${moment(fieldValue, DATE_FORMAT['datetime']).toISOString()}'`;
      }
      if (isQuery) {
        return `'${moment(fieldValue, DATE_FORMAT['datetime']).format(
          'YYYY-MM-DD HH:mm:ss.000'
        )}'`;
      }
      return `LocalDateTime.of(${moment(fieldValue, DATE_FORMAT['datetime'])
        .format('YYYY-M-D-H-m-s')
        .split('-')})`;
    } else {
      let time = `'${moment(fieldValue, DATE_FORMAT['time']).format(
        'HH:mm:ss'
      )}'`;
      if (isJsonField || isQuery) {
        return time;
      }
      return `LocalTime.parse(${time})`;
    }
  }

  function getCondition(rules, modalName) {
    const isBPM = isBPMQuery(parentType);
    const prefix = isBPM ? 'self' : modalName;
    const map_operators = MAP_OPERATOR[isBPM ? 'BPM' : expression];
    const returnValues = [];
    for (let i = 0; i < (rules && rules.length); i++) {
      const rule = rules[i];
      const { fieldName: propFieldName, field = {}, operator, allField } = rule;
      const { targetName, selectionList } = field || {};
      const type = field && field.type && field.type.toLowerCase();
      const typeName = field && field.typeName;
      const isNumber = ['long', 'integer', 'decimal', 'boolean'].includes(type);
      const isDateTime = ['date', 'time', 'datetime'].includes(type);
      let { fieldValue, fieldValue2, isRelationalValue } = rule;
      let fieldName = propFieldName;
      if (isNumber && !selectionList && !isRelationalValue) {
        if (!fieldValue) {
          fieldValue = 0;
        }
        if (['between', 'notBetween'].includes(operator) && !fieldValue2) {
          fieldValue2 = 0;
        }
        if (!fieldName || fieldName === '') {
          setAlert(true);
          returnValues.push(null);
          return;
        }
      }

      //check relational field
      const name = fieldName && fieldName.split(JOIN_OPERATOR[expression])[0];
      const f = allField && allField.find(f => f.name === name);
      const isRelational = [
        'many_to_many',
        'one_to_many',
        'many_to_one',
        'one_to_one',
        'json_many_to_many',
        'json_one_to_many',
        'json_many_to_one',
        'json_one_to_one',
      ].includes(
        f &&
          f.type &&
          f.type.toLowerCase() &&
          f.type.toLowerCase().replaceAll('-', '_')
      );
      if (isRelational) {
        returnValues.push(
          getRelationalCondition(rule, undefined, false, prefix)
        );
      } else {
        if (!isRelationalValue && !isNumber && typeof fieldValue !== 'object') {
          fieldValue = `'${jsStringEscape(fieldValue, withParam)}'`;
          fieldValue2 = `'${jsStringEscape(fieldValue2, withParam)}'`;
        }
        if (isDateTime) {
          if (!isRelationalValue) {
            fieldValue = getDateTimeValue(type, fieldValue);
            fieldValue2 = getDateTimeValue(type, fieldValue2);
          }
          fieldName = typeName ? `${fieldName}?.toLocalDateTime()` : fieldName;
        }
        const map_type = isBPM ? MAP_BPM_COMBINATOR : MAP_COMBINATOR;
        if (['in', 'notIn'].includes(operator)) {
          const value =
            rule &&
            rule.fieldValue &&
            rule.fieldValue
              .map(f =>
                isNumber
                  ? `${f['targetName'] || f['fullName'] || f['name']}`
                  : `'${f['targetName'] || f['fullName'] || f['name']}'`
              )
              .filter(f => f !== '')
              .join(',');
          returnValues.push(
            `${operator === 'notIn' ? '!' : ''}${`(${value})`}${
              JOIN_OPERATOR[expression]
            }${map_operators[operator]}(${prefix}${
              JOIN_OPERATOR[expression]
            }${fieldName}${
              selectionList
                ? ' '
                : `${JOIN_OPERATOR[expression]} ${targetName || 'fullName'}`
            })`
          );
        } else if (['between', 'notBetween'].includes(operator)) {
          if (operator === 'notBetween') {
            returnValues.push(
              `!(${prefix}${JOIN_OPERATOR[expression]}${fieldName} >= ${fieldValue} ${map_type['and']} ${prefix}${JOIN_OPERATOR[expression]}${fieldName} <= ${fieldValue2})`
            );
          }
          returnValues.push(
            `(${prefix}${JOIN_OPERATOR[expression]}${fieldName} >= ${fieldValue} ${map_type['and']} ${prefix}${JOIN_OPERATOR[expression]}${fieldName} <= ${fieldValue2})`
          );
        } else if (['isNotNull', 'isNull'].includes(operator)) {
          returnValues.push(
            `${prefix}${JOIN_OPERATOR[expression]}${fieldName} ${map_operators[operator]}`
          );
        } else if (['isTrue', 'isFalse'].includes(operator)) {
          const value = operator === 'isTrue' ? true : false;
          returnValues.push(
            `${prefix}${JOIN_OPERATOR[expression]}${fieldName} ${map_operators[operator]} ${value}`
          );
        } else if (['like', 'notLike'].includes(operator)) {
          returnValues.push(
            `${operator === 'notLike' ? '!' : ''}${prefix}${
              JOIN_OPERATOR[expression]
            }${fieldName}${JOIN_OPERATOR[expression]}${
              map_operators[operator]
            }(${fieldValue})`
          );
        } else {
          let value =
            typeof fieldValue === 'object' && fieldValue
              ? `'${jsStringEscape(
                  fieldValue[targetName] ||
                    fieldValue['fullName'] ||
                    fieldValue['name'] ||
                    '',
                  withParam
                )}'`
                ? `'${jsStringEscape(
                    fieldValue[targetName] ||
                      fieldValue['fullName'] ||
                      fieldValue['name'] ||
                      '',
                    withParam
                  )}'`
                : fieldValue['name']
              : fieldValue;
          returnValues.push(
            `${prefix}${JOIN_OPERATOR[expression]}${
              type === 'many_to_one' || type === 'json_many_to_one'
                ? `${fieldName}${JOIN_OPERATOR[expression]}${
                    field.targetName || 'fullName'
                  }`
                : fieldName
            } ${map_operators[operator]} ${value}`
          );
        }
      }
    }
    return returnValues;
  }

  const isRelationalCustomField = (field, parentFieldName) => {
    if (
      isBPMQuery(parentType) &&
      parentFieldName &&
      field &&
      field.allField &&
      field.allField.find(
        f =>
          f.name === parentFieldName &&
          RELATIONAL_TYPES.includes(f.type) &&
          (f.modelField ||
            f.model === 'com.axelor.meta.db.MetaJsonRecord' ||
            f.target === 'com.axelor.meta.db.MetaJsonRecord')
      )
    ) {
      return true;
    }
  };

  function checkValue({
    jsonFieldName,
    parentCustomField,
    field,
    value,
    values,
    targetName,
    operator,
    map_operators,
    rule,
  }) {
    let result = value;
    if (
      jsonFieldName &&
      MANY_TO_ONE_TYPES.includes(
        parentCustomField ? parentCustomField.type : field.type
      )
    ) {
      const { relatedValueFieldName } = rule || {};
      const modelName = getModelName(
        parentCustomField ? parentCustomField.target : field.target
      );
      const fieldValue = `${
        ['in', 'notIn'].includes(operator)
          ? `(${value || ''})`
          : ['like', 'notLike'].includes(operator)
          ? `CONCAT('%',${value},'%')`
          : value || ''
      }${
        modelName === 'MetaJsonRecord'
          ? ` and jsonModel = '${
              parentCustomField
                ? parentCustomField.jsonTarget
                : field.jsonTarget
            }'`
          : ''
      }`;
      const fieldPrefix = `select id from ${modelName}`;
      const isNullOperator = ['isNull', 'isNotNull'].includes(operator);
      const isINOperators = ['in', 'notIn'].includes(operator);
      if (['between', 'notBetween'].includes(operator)) {
        return `(${fieldPrefix}`;
      }
      result = `${isINOperators ? '' : `(`}${fieldPrefix} where ${
        values && values.length > 1
          ? `${values.slice(1).join(JOIN_OPERATOR[expression])}`
          : ''
      }${
        targetName
          ? `${values && values.length > 1 && !isNullOperator ? '.' : ''}${
              isNullOperator ? '' : targetName
            }`
          : ''
      } ${map_operators[operator]} ${fieldValue}${
        targetName && relatedValueFieldName && relatedValueFieldName.targetName
          ? `.${relatedValueFieldName.targetName}`
          : ''
      }${isINOperators ? '' : `)`}`;
    }
    return result;
  }

  /**
   * isField = param (For parameter fieldValue will be disable and :param will be values)
   */
  function getBPMCondition(rules, modalName) {
    const isBPM = isBPMQuery(parentType);
    const prefix = isBPM ? 'self' : modalName;
    const map_operators = MAP_OPERATOR[isBPM ? 'BPM' : expression];
    const returnValues = [];
    for (let i = 0; i < (rules && rules.length); i++) {
      const rule = rules[i];
      const { fieldName, field = {}, operator, allField, isField } = rule;
      const isParam = isField === 'param';
      const { targetName, selectionList, model, target, jsonField } =
        field || {};
      const type = field && field.type && field.type.toLowerCase();
      const name = fieldName && fieldName.split(JOIN_OPERATOR[expression])[0];
      const f = allField && allField.find(f => f.name === name);
      const isRelational = [
        'many_to_many',
        'one_to_many',
        'many_to_one',
        'one_to_one',
        'json_many_to_many',
        'json_one_to_many',
        'json_many_to_one',
        'json_one_to_one',
      ].includes(
        f &&
          f.type &&
          f.type.toLowerCase() &&
          f.type.toLowerCase().replaceAll('-', '_')
      );
      const isNumber = ['long', 'integer', 'decimal', 'boolean'].includes(type);
      const isDateTime = ['date', 'time', 'datetime'].includes(type);
      let isJsonField =
        model === 'com.axelor.meta.db.MetaJsonRecord' ||
        target === 'com.axelor.meta.db.MetaJsonRecord' ||
        jsonField;
      let parentCustomField;
      const values = fieldName && fieldName.split(JOIN_OPERATOR[expression]);
      if (values && values.length > 1) {
        let customField =
          allField &&
          allField.find(field => {
            let value =
              values &&
              values.find(
                name =>
                  field.name === name &&
                  (field.model === 'com.axelor.meta.db.MetaJsonRecord' ||
                    field.target === 'com.axelor.meta.db.MetaJsonRecord' ||
                    field.jsonField)
              );
            return value;
          });
        if (customField) {
          isJsonField = true;
          parentCustomField = customField;
        }
      }
      const jsonFieldName = isJsonField
        ? `${getJsonExpression(
            parentCustomField
              ? {
                  ...parentCustomField,
                  targetName: field && field.targetName,
                }
              : field,
            prefix,
            fieldName
          )}`
        : undefined;
      let {
        fieldValue,
        fieldValue2,
        isRelationalValue,
        relatedValueModal = {},
        relatedElseValueModal = {},
      } = rule || {};
      if (isNumber && !selectionList && !isRelationalValue) {
        if (!fieldValue) {
          fieldValue = 0;
        }
        if (['between', 'notBetween'].includes(operator) && !fieldValue2) {
          fieldValue2 = 0;
        }
        if (!fieldName || fieldName === '') {
          setAlert(true);
          returnValues.push(null);
          return;
        }
      }
      const relatedValueModalName = lowerCaseFirstLetter(
        relatedValueModal && relatedValueModal.name
      );
      const relatedElseValueModalName = lowerCaseFirstLetter(
        relatedElseValueModal && relatedElseValueModal.name
      );
      const isRelatedModalSame =
        (relatedValueModalName === modalName && isField === 'self') ||
        !withParam;
      const isRelatedElseModalSame =
        (relatedElseValueModalName === modalName && isField === 'self') ||
        !withParam;
      if (!['isNotNull', 'isNull'].includes(operator) && !isRelatedModalSame) {
        ++count;
      }
      if (isParam) {
        ++paramCount;
      }
      const isConditionalParamOption = isCondition && isParam;
      const isDateTimeContext =
        isConditionalParamOption &&
        DATE_TIME_VARIABLES.includes(relatedValueModal.name);
      const extractSyntax = isDateTimeContext && fieldValue;
      const frontExtractSyntax = extractSyntax && isDateTime;
      if (!isRelationalValue && !isNumber && typeof fieldValue !== 'object') {
        if (isConditionalParamOption) {
          fieldValue = `${jsStringEscape(fieldValue, withParam)}`;
          fieldValue2 = `${jsStringEscape(fieldValue2, withParam)}`;
        } else {
          fieldValue = `'${jsStringEscape(fieldValue, withParam)}'`;
          fieldValue2 = `'${jsStringEscape(fieldValue2, withParam)}'`;
        }
      }

      if (isDateTime) {
        if (!isRelationalValue) {
          fieldValue = getDateTimeValue(type, fieldValue, isJsonField);
          fieldValue2 = getDateTimeValue(type, fieldValue2, isJsonField);
        }
      }
      const dateCast =
        type === 'datetime' &&
        relatedValueModal?.name === '__date__' &&
        isConditionalParamOption &&
        !frontExtractSyntax;
      const valueDateCast =
        type === 'date' &&
        relatedValueModal?.name === '__datetime__' &&
        isConditionalParamOption &&
        !frontExtractSyntax;
      const includesTime = ['__datetime__', '__time__'].includes(
        relatedValueModal?.name
      );

      const map_type = isBPM ? MAP_BPM_COMBINATOR : MAP_COMBINATOR;
      if (['in', 'notIn'].includes(operator)) {
        let defaultValue =
          rule && rule.fieldValue && Array.isArray(rule.fieldValue)
            ? rule.fieldValue
                .map(f =>
                  isNumber || extractSyntax
                    ? `${f['targetName'] || f['fullName'] || f['name']}`
                    : `'${f['targetName'] || f['fullName'] || f['name']}'`
                )
                .filter(f => f !== '')
            : typeof fieldValue === 'object' && fieldValue
            ? `${jsStringEscape(
                fieldValue[field.targetName] || fieldValue['name'] || '',
                withParam
              )}`
            : rule.fieldValue;
        let value =
          checkValue({
            jsonFieldName,
            parentCustomField,
            field,
            value: isParam
              ? isCondition
                ? extractSyntax
                  ? `EXTRACT(${defaultValue.toString()} FROM CAST(? AS ${
                      includesTime ? `timestamp` : `date`
                    }))`
                  : valueDateCast
                  ? `DATE(?)`
                  : `?`
                : `:param${paramCount}`
              : defaultValue,
            values,
            targetName,
            operator,
            map_operators,
            rule,
          }) || defaultValue;
        returnValues.push({
          condition: `${
            frontExtractSyntax ? `EXTRACT(${defaultValue.toString()} FROM ` : ''
          }${dateCast ? 'DATE(' : ''}${
            jsonFieldName
              ? jsonFieldName
              : `${prefix}.${fieldName}${
                  selectionList || isConditionalParamOption
                    ? ''
                    : `.${targetName || 'fullName'}`
                }`
          }${dateCast ? ') ' : ''}${frontExtractSyntax ? `)` : ''} ${
            jsonFieldName ? 'IN' : map_operators[operator]
          } ${
            isRelatedModalSame
              ? withParam
                ? jsonFieldName
                  ? value
                  : fieldValue
                : `(${value})`
              : `?${count}`
          }`,
          values: isRelatedModalSame
            ? undefined
            : [
                [
                  isParam
                    ? isCondition
                      ? valueDateCast
                        ? `DATE(?)`
                        : `?`
                      : `:param${paramCount}`
                    : value,
                ],
              ],
          resultParams: isConditionalParamOption
            ? isDateTimeContext
              ? [relatedValueModal?.name]
              : [fieldValue || relatedValueModal?.name]
            : null,
        });
      } else if (['between', 'notBetween'].includes(operator)) {
        const extractFieldName =
          fieldValue && fieldValue.name && fieldValue.name.toString();
        let values =
          isRelatedModalSame && isRelatedElseModalSame
            ? undefined
            : isRelatedModalSame
            ? [
                isParam
                  ? isCondition
                    ? extractSyntax
                      ? `EXTRACT(${extractFieldName} FROM CAST(? AS ${
                          includesTime ? `timestamp` : `date`
                        }))`
                      : valueDateCast
                      ? `DATE(?)`
                      : `?`
                    : `:param${paramCount}`
                  : fieldValue2,
              ]
            : isRelatedElseModalSame
            ? [
                isParam
                  ? isCondition
                    ? extractSyntax
                      ? `EXTRACT(${extractFieldName} FROM CAST(? AS ${
                          includesTime ? `timestamp` : `date`
                        }))`
                      : valueDateCast
                      ? `DATE(?)`
                      : `?`
                    : `:param${paramCount}`
                  : fieldValue,
              ]
            : [
                isParam
                  ? isCondition
                    ? extractSyntax
                      ? `EXTRACT(${extractFieldName} FROM CAST(? AS ${
                          includesTime ? `timestamp` : `date`
                        }))`
                      : valueDateCast
                      ? `DATE(?)`
                      : `?`
                    : `:param${paramCount}`
                  : fieldValue,
                isParam
                  ? isCondition
                    ? extractSyntax
                      ? `EXTRACT(${extractFieldName} FROM CAST(? AS ${
                          includesTime ? `timestamp` : `date`
                        }))`
                      : valueDateCast
                      ? `DATE(?)`
                      : `?`
                    : `:param${++paramCount}`
                  : fieldValue2,
              ];
        let value = checkValue({
          jsonFieldName,
          parentCustomField,
          field,
          value: null,
          values: fieldName && fieldName.split(JOIN_OPERATOR[expression]),
          targetName,
          operator,
          map_operators,
          rule,
        });
        if (isDateTime && isBPM) {
          returnValues.push({
            condition: `${
              frontExtractSyntax ? `EXTRACT(${extractFieldName} FROM ` : ''
            }${dateCast ? 'DATE(' : ''}${
              jsonFieldName
                ? `${jsonFieldName}${value ? ' IN ' : ''}`
                : `${prefix}.${fieldName}`
            }${value ? `${value}` : ''}${targetName ? `.${targetName}` : ''}${
              dateCast ? ')' : ''
            }${frontExtractSyntax ? ') ' : ''}${
              operator === 'notBetween' ? ' NOT BETWEEN' : ' BETWEEN'
            } ${
              isRelatedModalSame
                ? isParam
                  ? isCondition
                    ? extractSyntax
                      ? `EXTRACT(${extractFieldName} FROM CAST(? AS ${
                          includesTime ? `timestamp` : `date`
                        }))`
                      : valueDateCast
                      ? `DATE(?)`
                      : `?`
                    : `:param${paramCount}`
                  : fieldValue
                : `?${count}`
            } ${map_type['and']} ${
              isRelatedElseModalSame
                ? isParam
                  ? isCondition
                    ? extractSyntax
                      ? `EXTRACT(${extractFieldName} FROM CAST(? AS ${
                          includesTime ? `timestamp` : `date`
                        }))`
                      : valueDateCast
                      ? `DATE(?)`
                      : `?`
                    : `:param${++paramCount}`
                  : fieldValue2
                : `?${++count}`
            }${value ? ')' : ''}`,
            values,
            resultParams: isConditionalParamOption
              ? isDateTimeContext
                ? [relatedValueModal?.name]
                : [
                    fieldValue || relatedValueModal?.name,
                    fieldValue2 || relatedElseValueModal?.name,
                  ]
              : null,
          });
        } else {
          if (value) {
            let names = fieldName && fieldName.split(JOIN_OPERATOR[expression]);
            returnValues.push({
              condition: `(${jsonFieldName}${value ? ' IN ' : ''}${
                value ? value : ''
              }${operator === 'notBetween' ? ' NOT(' : ''} self${
                names && names.length > 1
                  ? `.${names.slice(1).join(JOIN_OPERATOR[expression])}`
                  : ''
              }${targetName ? `.${targetName}` : ''} >= ${
                isRelatedModalSame
                  ? isParam
                    ? isCondition
                      ? valueDateCast
                        ? `DATE(?)`
                        : `?`
                      : `:param${paramCount}`
                    : fieldValue
                  : `?${count}`
              } ${map_type['and']} self${
                names && names.length > 1
                  ? `.${names.slice(1).join(JOIN_OPERATOR[expression])}`
                  : ''
              }${targetName ? `.${targetName}` : ''} <= ${
                isRelatedElseModalSame
                  ? isParam
                    ? isCondition
                      ? valueDateCast
                        ? `DATE(?)`
                        : `?`
                      : `:param${++paramCount}`
                    : fieldValue2
                  : `?${++count}`
              })${value ? ')' : ''}${operator === 'notBetween' ? ')' : ''}`,
              values,
              resultParams: isConditionalParamOption
                ? isDateTimeContext
                  ? [relatedValueModal?.name]
                  : [
                      fieldValue || relatedValueModal?.name,
                      fieldValue2 || relatedElseValueModal?.name,
                    ]
                : null,
            });
          }
          returnValues.push({
            condition: `${operator === 'notBetween' ? 'NOT ' : ''}(${
              frontExtractSyntax ? `EXTRACT(${extractFieldName} FROM ` : ''
            }${jsonFieldName ? jsonFieldName : `${prefix}.${fieldName}`}${
              frontExtractSyntax ? ')' : ''
            } >= ${
              isRelatedModalSame
                ? isParam
                  ? isCondition
                    ? extractSyntax
                      ? `EXTRACT(${extractFieldName} FROM CAST(? AS ${
                          includesTime ? `timestamp` : `date`
                        }))`
                      : valueDateCast
                      ? `DATE(?)`
                      : `?`
                    : `:param${paramCount}`
                  : fieldValue
                : `?${count}`
            } ${map_type['and']} ${
              frontExtractSyntax ? `EXTRACT(${extractFieldName} FROM ` : ''
            }${jsonFieldName ? jsonFieldName : `${prefix}.${fieldName}`}${
              frontExtractSyntax ? ') ' : ''
            } <= ${
              isRelatedElseModalSame
                ? isParam
                  ? isCondition
                    ? extractSyntax
                      ? `EXTRACT(${extractFieldName} FROM CAST(? AS ${
                          includesTime ? `timestamp` : `date`
                        }))`
                      : valueDateCast
                      ? `DATE(?)`
                      : `?`
                    : `:param${++paramCount}`
                  : fieldValue2
                : `?${++count}`
            })`,
            values,
            resultParams: isConditionalParamOption
              ? isDateTimeContext
                ? [relatedValueModal?.name]
                : [
                    fieldValue || relatedValueModal?.name,
                    fieldValue2 || relatedElseValueModal?.name,
                  ]
              : null,
          });
        }
      } else if (['isNotNull', 'isNull'].includes(operator)) {
        const isSubQuery =
          jsonFieldName &&
          MANY_TO_ONE_TYPES.includes(
            parentCustomField ? parentCustomField.type : field.type
          );
        const multipleFields = fieldName && fieldName.split('.');
        const isNestedField = multipleFields && multipleFields.length > 1;
        let value, defaultValue;
        if (isSubQuery && isNestedField) {
          value =
            rule &&
            rule.fieldValue &&
            rule.fieldValue
              .map(f =>
                isNumber
                  ? `${f['targetName'] || f['fullName'] || f['name']}`
                  : `'${f['targetName'] || f['fullName'] || f['name']}'`
              )
              .filter(f => f !== '');
          defaultValue = value;
          value = checkValue({
            jsonFieldName,
            parentCustomField,
            field,
            value: isParam
              ? isCondition
                ? `?`
                : `:param${paramCount}`
              : value,
            values,
            targetName,
            operator,
            map_operators,
            rule,
          });
        }
        returnValues.push({
          condition: `${dateCast ? 'DATE(' : ''}${
            jsonFieldName ? jsonFieldName : `${prefix}.${fieldName}`
          } ${dateCast ? ') ' : ''}${
            isSubQuery && isNestedField
              ? `IN ${value}`
              : map_operators[operator]
          }`,
          resultParams: isConditionalParamOption ? defaultValue : null,
        });
      } else if (['isTrue', 'isFalse'].includes(operator)) {
        --count;
        let defaultValue =
          operator === 'isTrue'
            ? true
            : operator === 'isFalse'
            ? false
            : operator;
        let value =
          checkValue({
            jsonFieldName,
            parentCustomField,
            field,
            value: isParam
              ? isCondition
                ? extractSyntax
                  ? `EXTRACT(${defaultValue.toString()} FROM CAST(? AS ${
                      includesTime ? `timestamp` : `date`
                    }))`
                  : valueDateCast
                  ? `DATE(?)`
                  : `?`
                : `:param${paramCount}`
              : `${defaultValue}`,
            values,
            targetName,
            operator,
            map_operators,
            rule,
          }) || defaultValue;
        const isRelationalCustom = isRelationalCustomField(
          rule,
          values && values[0]
        );
        returnValues.push({
          condition: `${
            frontExtractSyntax ? `EXTRACT(${defaultValue.toString()} FROM ` : ''
          }${dateCast ? 'DATE(' : ''}${
            jsonFieldName ? jsonFieldName : `${prefix}.${fieldName}`
          }${dateCast ? ') ' : ''}${frontExtractSyntax ? `)` : ''} ${
            (jsonFieldName && isRelational) || isParam
              ? isRelationalCustom
                ? 'IN'
                : '='
              : map_operators[operator]
          } ${
            isRelatedModalSame
              ? withParam
                ? isParam
                  ? isCondition
                    ? extractSyntax
                      ? `EXTRACT(${defaultValue.toString()} FROM CAST(? AS ${
                          includesTime ? `timestamp` : `date`
                        }))`
                      : valueDateCast
                      ? `DATE(?)`
                      : `?`
                    : `:param${paramCount}`
                  : fieldValue
                : value
              : `?${count}`
          }`,
          values: isRelatedModalSame
            ? undefined
            : [
                isParam
                  ? isCondition
                    ? valueDateCast
                      ? `DATE(?)`
                      : `?`
                    : `:param${paramCount}`
                  : value,
              ],
          resultParams: isConditionalParamOption
            ? isDateTimeContext
              ? relatedValueModal?.name
              : fieldValue
            : null,
        });
      } else if (['contains', 'notContains'].includes(operator)) {
        let value =
          typeof fieldValue === 'object' && fieldValue
            ? `'${jsStringEscape(
                fieldValue[field.targetName] || '',
                withParam
              )}'`
              ? `'${jsStringEscape(
                  fieldValue[field.targetName] || '',
                  withParam
                )}'`
              : fieldValue['name']
            : fieldValue;
        returnValues.push({
          condition: `${dateCast ? 'DATE(' : ''}${
            isRelatedModalSame
              ? withParam
                ? isParam
                  ? isCondition
                    ? valueDateCast
                      ? `DATE(?)`
                      : `?`
                    : `:param${paramCount}`
                  : fieldValue
                : value
              : `?${count}`
          }${dateCast ? ') ' : ''}${map_operators[operator]} ${
            jsonFieldName ? jsonFieldName : `${prefix}.${fieldName}`
          }`,
          values: isRelatedModalSame
            ? undefined
            : [
                isParam
                  ? isCondition
                    ? valueDateCast
                      ? `DATE(?)`
                      : `?`
                    : `:param${paramCount}`
                  : value,
              ],
          resultParams: isConditionalParamOption ? value : null,
        });
      } else {
        let defaultValue =
          typeof fieldValue === 'object' && fieldValue
            ? `'${jsStringEscape(
                fieldValue[field.targetName] || fieldValue['name'] || '',
                withParam
              )}'`
              ? `'${jsStringEscape(
                  fieldValue[field.targetName] || fieldValue['name'] || '',
                  withParam
                )}'`
              : fieldValue['name']
            : fieldValue;
        let value =
          checkValue({
            jsonFieldName,
            parentCustomField,
            field,
            value: isParam
              ? isCondition
                ? extractSyntax
                  ? `EXTRACT(${defaultValue.toString()} FROM CAST(? AS ${
                      includesTime ? `timestamp` : `date`
                    }))`
                  : valueDateCast
                  ? `DATE(?)`
                  : `?`
                : `:param${paramCount}`
              : `${defaultValue}`,
            values,
            targetName,
            operator,
            map_operators,
            rule,
          }) || defaultValue;
        /**
         * For custom field equals and not equals operator
         * should be IN and NOT IN
         * as subquery can return multiple records with equal operator
         */
        const isRelationalCustom = isRelationalCustomField(
          rule,
          values && values[0]
        );
        returnValues.push({
          condition: `${
            frontExtractSyntax ? `EXTRACT(${defaultValue.toString()} FROM ` : ''
          }${dateCast ? 'DATE(' : ''}${
            jsonFieldName
              ? jsonFieldName
              : `${prefix}.${
                  [
                    'many_to_one',
                    'json_many_to_one',
                    'one_to_one',
                    'json_one_to_one',
                  ].includes(type) && !isRelationalValue
                    ? `${fieldName}${
                        isConditionalParamOption
                          ? ''
                          : `.${field.targetName || 'fullName'}`
                      }`
                    : fieldName
                }`
          }${dateCast ? ')' : ''}${frontExtractSyntax ? `)` : ''} ${
            jsonFieldName && isRelational
              ? isRelationalCustom
                ? 'IN'
                : '='
              : map_operators[operator]
          } ${
            isRelatedModalSame
              ? ['like', 'notLike'].includes(operator) &&
                (!jsonFieldName || (jsonFieldName && !isRelationalCustom))
                ? `CONCAT('%',${
                    isParam
                      ? isCondition
                        ? extractSyntax
                          ? `EXTRACT(${defaultValue.toString()} FROM CAST(? AS ${
                              includesTime ? `timestamp` : `date`
                            }))`
                          : valueDateCast
                          ? `DATE(?)`
                          : `?`
                        : `:param${paramCount}`
                      : fieldValue
                  },'%')`
                : withParam
                ? jsonFieldName
                  ? value
                  : isParam
                  ? isCondition
                    ? extractSyntax
                      ? `EXTRACT(${defaultValue.toString()} FROM CAST(? AS ${
                          includesTime ? `timestamp` : `date`
                        }))`
                      : valueDateCast
                      ? `DATE(?)`
                      : `?`
                    : `:param${paramCount}`
                  : fieldValue
                : value
              : ['like', 'notLike'].includes(operator) &&
                (!jsonFieldName ||
                  (jsonFieldName && !MANY_TO_ONE_TYPES.includes(operator)))
              ? `CONCAT('%',?${count},'%')`
              : `?${count}`
          }`,
          values: isRelatedModalSame
            ? undefined
            : [
                isParam
                  ? isCondition
                    ? extractSyntax
                      ? `EXTRACT(${defaultValue.toString()} FROM CAST(? AS ${
                          includesTime ? `timestamp` : `date`
                        }))`
                      : valueDateCast
                      ? `DATE(?)`
                      : `?`
                    : `:param${paramCount}`
                  : value,
              ],
          resultParams: isConditionalParamOption
            ? isDateTimeContext
              ? relatedValueModal?.name
              : !defaultValue
              ? relatedValueModal?.name
              : defaultValue
            : null,
        });
      }
    }
    return returnValues;
  }

  function getBPMCriteria(rule, modalName, isChildren) {
    const { rules, combinator = 'and', children } = (rule && rule[0]) || {};
    const bpmConditions = getBPMCondition(rules, modalName);
    const condition = (bpmConditions && bpmConditions.filter(f => f)) || [];
    const childrenConditions = [];
    children &&
      children.length > 0 &&
      children.forEach(child => {
        const {
          condition: conditions,
          values,
          resultParams,
        } = getBPMCriteria([child], modalName, true) || {};
        const newValues = [].concat.apply([], values);
        childrenConditions.push({
          condition: conditions,
          values: newValues && newValues.length > 0 ? newValues : undefined,
          resultParams,
        });
      });
    const map_type = isBPMQuery(parentType)
      ? MAP_BPM_COMBINATOR
      : MAP_COMBINATOR;
    const c = condition && condition.map(co => co && co.condition);
    const childConditions =
      childrenConditions &&
      (childrenConditions.map(co => co && co.condition) || []).filter(f => f);
    const childValues =
      childrenConditions && childrenConditions.filter(val => val !== null);
    const resultParams =
      condition &&
      (condition.map(co => co && co.resultParams) || []).filter(f => f);
    if (children.length > 0) {
      let isChild = childConditions && childConditions.length > 0;
      return {
        condition: `${isChild ? '(' : ''}${
          c ? c.join(' ' + map_type[combinator] + ' ') : ''
        } ${
          isChild
            ? `${map_type[combinator]} ${childConditions.join(
                ' ' + map_type[combinator] + ' '
              )}`
            : ''
        }${isChild ? ')' : ''}`,
        values: [
          ...((condition &&
            condition.map(co => co && co.values).filter(f => f)) ||
            []),
          ...((childValues &&
            childValues.map(co => co && co.values).filter(f => f)) ||
            []),
        ],
        resultParams: [
          ...resultParams,
          ...(childrenConditions &&
            (childrenConditions.map(co => co && co.resultParams) || []).filter(
              f => f
            )),
        ],
      };
    } else if (isChildren && condition && c && c.length !== 0) {
      return {
        condition: `(${c.join(' ' + map_type[combinator] + ' ')})`,
        values:
          condition && condition.map(co => co && co.values).filter(f => f),
        resultParams,
      };
    } else {
      return {
        condition: c.join(' ' + map_type[combinator] + ' '),
        values:
          condition && condition.map(co => co && co.values).filter(f => f),
        resultParams,
      };
    }
  }

  function getCriteria(rule, modalName, isChildren) {
    const { rules, combinator = 'and', children } = rule[0];
    const condition = (getCondition(rules, modalName) || []).filter(
      f => f !== ''
    );
    const childrenConditions = [];
    children &&
      children.length > 0 &&
      children.forEach(child => {
        const conditions = getCriteria([child], modalName, true);
        if (conditions) {
          childrenConditions.push(conditions);
        }
      });
    const map_type = isBPMQuery(parentType)
      ? MAP_BPM_COMBINATOR
      : MAP_COMBINATOR;

    if (children.length > 0 && condition && condition.length > 0) {
      let isChild = childrenConditions && childrenConditions.length !== 0;
      return `${isChild ? '(' : ''}${
        condition ? condition.join(' ' + map_type[combinator] + ' ') : ''
      } ${
        isChild
          ? ` ${map_type[combinator]} ${childrenConditions.join(
              ' ' + map_type[combinator] + ' '
            )}`
          : ''
      }${isChild ? ')' : ''}`;
    } else if (isChildren && condition && condition.length > 0) {
      return '(' + condition.join(' ' + map_type[combinator] + ' ') + ')';
    } else if (condition && condition.length > 0) {
      return condition.join(' ' + map_type[combinator] + ' ');
    }
  }

  function getListOfTree(list) {
    var map = {},
      node,
      roots = [];
    const rules =
      list &&
      list.map((item, index) => {
        map[item.id] = index;
        return { ...item, children: [] };
      });
    for (let i = 0; i < rules.length; i += 1) {
      node = rules[i];
      if (node.parentId >= 0) {
        rules[map[node.parentId]] &&
          rules[map[node.parentId]].children.push(node);
      } else {
        roots.push(node);
      }
    }
    return roots;
  }

  const onChange = React.useCallback(function onChange(value, index) {
    setExpressionComponents(
      produce(draft => {
        if (typeof value === 'function') {
          !draft[index].value && (draft[index].value = {});
          value(draft[index].value);
        } else {
          draft[index].value = value;
        }
      })
    );
  }, []);

  const save = async (expr, expressionValues, resultParams) => {
    if (onSave) {
      onSave(expr, expressionValues, resultParams);
    }
    if (!model) return;
    let payload = {
      ...(record || {}),
      [resultField]: expr && expr.trim(),
      [resultMetaField]: expressionValues
        ? JSON.stringify(expressionValues)
        : expressionValues,
    };
    if (resultParam) {
      payload[resultParam] = [].concat(...resultParams).toString();
    }
    await saveRecord(model, payload);
    const closeElementArray =
      window.top &&
      window.top.document &&
      window.top.document.getElementsByClassName('button-close');
    const closeElement =
      closeElementArray && closeElementArray[closeElementArray.length - 1];
    closeElement && closeElement.click && closeElement.click();
  };

  const checkValidation = () => {
    let isValid = true;
    const nodes = [];
    for (
      let i = 0;
      i < (expressionComponents && expressionComponents.length);
      i++
    ) {
      const component = expressionComponents[i];
      const { value } = component;
      const { rules, metaModals } = value || {};
      if (!metaModals && !isPackage) {
        return isValid;
      }
      nodes.push(rules);
    }
    const parentNodes = flattenDeep(nodes || []);
    const rules = parentNodes && parentNodes.map(n => n.rules);
    const allRules = flattenDeep(rules || []);
    for (let i = 0; i < (allRules && allRules.length); i++) {
      const rule = allRules && allRules[i];
      const {
        fieldName: propFieldName,
        field = {},
        operator,
        relatedValueModal,
        relatedElseValueModal,
      } = rule || {};
      const { selectionList } = field || {};
      const type = field && field.type && field.type.toLowerCase();
      const isNumber = [
        'long',
        'integer',
        'decimal',
        'boolean',
        'button',
        'double',
      ].includes(type);
      let { fieldValue, fieldValue2, isRelationalValue, isField } = rule;
      let fieldName = propFieldName;
      if (isNumber && !selectionList && !isRelationalValue) {
        if (!fieldValue && fieldValue !== false) {
          fieldValue = 0;
        }
        if (['between', 'notBetween'].includes(operator) && !fieldValue2) {
          fieldValue2 = 0;
        }
      }

      const fValue = isNaN(fieldValue) ? fieldValue : `${fieldValue}`;
      if (!fieldName) {
        isValid = false;
        break;
      }
      if (isCondition && isField === 'param' && relatedValueModal?.name) {
        if (
          ['between', 'notBetween'].includes(operator) &&
          relatedValueModal?.name === '__user__' &&
          !relatedElseValueModal?.name
        ) {
          isValid = false;
        }
        break;
      }
      if (isEmpty(fValue)) {
        if (!['isNull', 'isNotNull', 'isTrue', 'isFalse'].includes(operator)) {
          isValid = false;
          break;
        }
      }
      if (
        !operator ||
        (selectionList &&
          !fieldValue &&
          !['isNull', 'isNotNull', 'isTrue', 'isFalse'].includes(operator)) ||
        (isNumber && isRelationalValue && !fieldValue) ||
        (((!isNumber && !fieldValue) ||
          (fieldValue && Array.isArray(fieldValue) && fieldValue.length <= 0) ||
          (((!isNumber && !fieldValue2) ||
            (fieldValue2 &&
              Array.isArray(fieldValue2) &&
              fieldValue2.length <= 0)) &&
            ['between', 'notBetween'].includes(operator))) &&
          !['isNull', 'isNotNull', 'isTrue', 'isFalse'].includes(operator))
      ) {
        if (!isCondition && isField === 'param') {
          isValid = true;
        } else {
          isValid = false;
          break;
        }
      }
    }
    return isValid;
  };

  function generateExpression(combinator, type) {
    const expressionValues = [];
    let model;
    let vals = [],
      resultParams = [];
    const expressions = [];
    const isValid = checkValidation();
    if (!isValid) {
      setAlert(true);
      return;
    }
    for (let i = 0; i < expressionComponents.length; i++) {
      const component = expressionComponents[i];
      const { value } = component;
      const { rules, metaModals } = value || {};
      const modalName = metaModals && metaModals.name;
      const { fullName, type: modelType } = metaModals || {};
      model = isBPMQuery(type)
        ? modelType === 'metaJsonModel'
          ? modalName
          : fullName
        : modalName;
      let str = '';
      const listOfTree = getListOfTree(rules);
      const criteria = isBPMQuery(type)
        ? getBPMCriteria(listOfTree, lowerCaseFirstLetter(modalName), undefined)
        : getCriteria(listOfTree, lowerCaseFirstLetter(modalName), undefined);
      vals.push(
        ...((criteria &&
          ((criteria.values || []).filter(f => Array.isArray(f)) || [])) ||
          [])
      );
      resultParams.push(...(criteria && (criteria.resultParams || [])));
      if (metaModals || isPackage) {
        str += isBPMQuery(type) ? criteria && criteria.condition : criteria;
      } else {
        break;
      }
      let expressionValue = {
        metaModalName: modalName,
        metaModalType: metaModals && metaModals.type,
        rules,
      };
      expressionValues.push(expressionValue);
      expressions.push(`${str}`);
    }

    const map_type = isBPMQuery(parentType)
      ? MAP_BPM_COMBINATOR
      : MAP_COMBINATOR;

    const str = (expressions.filter(e => e !== '') || [])
      .map(e => (expressions.length > 1 ? `(${e})` : e))
      .join(' ' + map_type[combinator] + ' ');

    let expr = str;
    if (isBPMQuery(type)) {
      let valueParameters = '';
      vals &&
        vals.forEach(v => {
          if (v && Array.isArray(v[0]) && v[0]) {
            valueParameters = valueParameters + `, (${v[0]})`;
          } else {
            if (v && Array.isArray(v) && v.length > 0) {
              v = v.join(', ');
            }
            valueParameters = valueParameters + ', ' + v;
          }
        });

      const showBracket = !queryModel || withParam;
      expr = str
        ? `${showBracket ? '(' : ''}${
            !queryModel ? `${withParam ? `"${model}"` : `${model}`}, ` : ''
          }${withParam ? `" ${str} "` : ` ${str} `}${
            vals && vals.length > 0 ? `${valueParameters}` : ``
          }${showBracket ? ')' : ''}`
        : null;
    }
    paramCount = 0;
    count = 0;
    save(
      expr,
      expr
        ? {
            values: expressionValues,
            combinator,
          }
        : null,
      resultParams
    );
  }

  const setQueryModel = React.useCallback(async () => {
    if (!queryModel || !isBPMQuery(parentType)) return;
    const expressionComponents = [];
    const modelName = queryModel.split('.') || [];
    const length = modelName.length;
    const criteria = {
      criteria: [
        {
          fieldName: 'name',
          operator: '=',
          value: length > 1 ? modelName[length - 1] : queryModel,
        },
      ],
      operator: 'and',
    };
    const metaModels = await getModels(
      criteria,
      length > 1 ? 'metaModel' : 'metaJsonModel'
    );
    if (!metaModels) return;
    const value = {
      metaModals: metaModels && metaModels[0],
      rules: [
        {
          id: 0,
          parentId: -1,
          combinator: 'and',
          rules: [{}],
        },
      ],
    };
    expressionComponents.push({
      Component: ExpressionComponent,
      value,
    });
    setDefaultExpressionValue(value);
    setExpressionComponents(expressionComponents);
  }, [queryModel, parentType]);

  const setData = React.useCallback(
    async resultMetaFieldValues => {
      const { values, combinator } = resultMetaFieldValues || {};
      const expressionComponents = [];
      if (!values || values.length === 0) {
        await setQueryModel();
        return;
      }
      for (let i = 0; i < values.length; i++) {
        const element = values[i];
        const { metaModalName, metaModalType } = element;
        if (!metaModalName && !isPackage) return;
        const criteria = {
          criteria: [
            {
              fieldName: 'name',
              operator: '=',
              value: metaModalName,
            },
          ],
          operator: 'and',
        };
        const metaModels = await getModels(criteria, metaModalType);
        if (!metaModels && !isPackage) return;
        const value = {
          metaModals: metaModels && metaModels[0],
          rules: element.rules,
        };
        expressionComponents.push({
          Component: ExpressionComponent,
          value,
        });
      }
      setExpressionComponents(expressionComponents);
      setCombinator(combinator || 'and');
    },
    [isPackage, setQueryModel, setExpressionComponents, setCombinator]
  );

  useEffect(() => {
    if (exprVal) {
      setData(exprVal);
    }
  }, [exprVal, setData]);

  useEffect(() => {
    async function fetchValue() {
      if (!model || !id) {
        await setQueryModel();
        return;
      }
      const record = await getRecord(model, id);
      setRecord(record);
      if (!record) {
        await setQueryModel();
        return;
      }
      const resultMetaFieldValues = record && record[resultMetaField];
      setData(JSON.parse(resultMetaFieldValues || '{}'));
    }
    fetchValue();
  }, [resultMetaField, id, model, setQueryModel, setData]);

  return (
    <div className={classes.container}>
      <div className={classes.root}>
        <Paper variant="outlined" className={classes.paper}>
          <div className={classes.expression}>
            <Timeline
              title={
                <Select
                  className={classes.combinator}
                  name="expression"
                  value={combinator}
                  options={COMBINATORS}
                  onChange={setCombinator}
                  disableUnderline={true}
                />
              }
            >
              <Button
                title={isBPMQuery(parentType) ? 'Add group' : 'Add Expression'}
                Icon={AddIcon}
                onClick={() => onAddExpressionEditor()}
                disabled={
                  isBPMQuery(parentType)
                    ? expressionComponents &&
                      ((expressionComponents[0] &&
                        expressionComponents[0].value &&
                        expressionComponents[0].value.metaModals) ||
                        expressionComponents.length === 0)
                      ? false
                      : true
                    : false
                }
              />
              <div>
                {expressionComponents &&
                  expressionComponents.map(({ value }, index) => {
                    return (
                      <div className={classes.expressionContainer} key={index}>
                        <ExpressionComponent
                          value={value}
                          index={index}
                          onChange={onChange}
                          element={record && modelFilter && record[modelFilter]}
                          type={parentType}
                          queryModel={queryModel}
                          isCondition={isCondition}
                          isPackage={isPackage}
                          isParameterShow={isParameterShow}
                          parameters={parameters}
                        />
                        <IconButton
                          size="small"
                          onClick={() => onRemoveExpressionEditor(index)}
                        >
                          <DeleteIcon />
                        </IconButton>
                      </div>
                    );
                  })}
              </div>
            </Timeline>
          </div>
        </Paper>
      </div>

      <div className={classes.actions}>
        <Button
          title="Save"
          className={classes.save}
          onClick={() => generateExpression(combinator, parentType)}
        />
        {dialogActionButton && (
          <React.Fragment>{dialogActionButton}</React.Fragment>
        )}
      </div>

      <Dialog
        open={openAlert}
        onClose={() => setAlert(false)}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        classes={{
          paper: classes.dialog,
        }}
      >
        <DialogTitle id="alert-dialog-title">Error</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Add all values
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button title="Ok" onClick={() => setAlert(false)} />
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default ExpressionBuilder;
